import Vue from 'vue'
import Router from 'vue-router'
//components
import TopicList from '@/pages/topic/list.vue'
import AddTopic from '@/pages/topic/add.vue'
import EditTopic from '@/pages/topic/edit.vue'
import Login from '@/pages/login/index.vue'
import Layout from '@/pages/layout/index.vue'
import TagList from '@/pages/tag/list.vue'
import AddTag from '@/pages/tag/add.vue'
import EditTag from '@/pages/tag/edit.vue'
import AnswerList from '@/pages/answer/list.vue'
import AnswerDetail from '@/pages/answer/detail.vue'
import Question from '@/pages/question/list.vue'
import QuestionDetail from '@/pages/question/detail.vue'
import QuestionAdd from '@/pages/question/add.vue'
import AnswersComments from '@/pages/question/answerscomments.vue'
import CommentsOfAnswer from '@/pages/question/commentsofanswer.vue'
import AnswerManageList from '@/pages/answerManage/list.vue'
import AnswerEdit from '@/pages/answerManage/edit.vue'
import AnswerAdd from '@/pages/answerManage/add.vue'
import ImageMore from '@/pages/imageMore/list.vue'
import WeiXin from '@/pages/weixin-test/index.vue'
import DdUser from '@/pages/ddUser/ddUser.vue'
import AnswerInfo from '@/pages/question/answerinfo'
import Activity from '@/pages/activity/activity'
import Message from '@/pages/message/list'
import NewMessage from '@/pages/message/newMessage'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {path: '/login', component: Login},
        {
            path: '/', component: Layout, redirect: '/questionList',
            children: [
                {path: 'topicList', component: TopicList},
                {path: 'addTopic', component: AddTopic, meta: {keepAlive: true}},
                {path: 'tagList', component: TagList},
                {path: 'addTag/', name: 'addTag', component: AddTag},
                {path: 'editTag/:tagId', name: 'editTag', component: EditTag},
                {path: 'editTopic/:topicId', component: EditTopic, props: true},
                {path: 'topicDetail/:topicId', component: EditTopic, props: true},


                {path: 'questionList', component: Question},
                {path: 'questionAdd', component: QuestionAdd},
                {path: 'question/:questionId', name: 'questionDetail', component: QuestionDetail},
                {path: 'question/:questionId/answerscomments', name: 'answerscomments', component: AnswersComments},
                {path: 'question/:questionId/answers/:answerId/comments', component: CommentsOfAnswer},
                {path: 'answerInfo/:questionId', name: 'answerInfo', component: AnswerInfo, props: true},
                //答主相关
                {path: 'answerManageList', component: AnswerManageList},
                {path: 'answerEdit/:answerId', component: AnswerEdit},
                {path: 'answerAdd', component: AnswerAdd},
                {path: 'answer/:answerId', component: AnswerDetail},
                {path: 'answerList', component: AnswerList},


                {path: 'imageMoreList', component: ImageMore},
                {path: 'ddUserList', component: DdUser},

                //微信页面测试
                {path: 'weixin', component: WeiXin},

                {path:'activity',component:Activity},
                {path:'messageList',component:Message},
                {path:'newMessage',component:NewMessage,name:'newMessage'},

            ]
        },
    ]
})
